// Normalize Styles
@import "node_modules/normalize.css/normalize";

// Import Modules
@import "../_modules/link/link";

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.yeogurt-info {
  text-align: center;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto; }

.version {
  font-size: 14px;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  position: absolute;
  bottom: 10px;
  right: 10px; }

.logo {
  margin-top: 15px; }

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0; }

$height: 650px;
$width: 450px;
.poster {
  position: absolute;
  display: block;
  height: $height;
  width: $width;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: #EAE5E0;
  overflow: hidden; }

$luce_dim : 1000px;
$radius : 50px;

#luce {
  position: absolute;
  bottom: 40%;
  right: 10%;
  width: $luce_dim;
  height: $luce_dim;
  display: block;
  background-color: #CC312A;
  border-bottom-right-radius: $radius;
  transform-origin: 950px 950px;
  //transform: rotate(-45deg)
  overflow: hidden;
  animation: rotation 6s infinite;
  #img {
    position: absolute;
    height: 100px;
    width: 100px;
    bottom: 0;
    right: 0;
    animation: rotation-inverse 6s linear;
    animation-iteration-count: infinite; } }

#text_1 {
  position: absolute;
  top: 20%;
  left: 20%;
  width: 80%;
  transform: rotate(-25deg); }
#text_2 {
  position: absolute;
  top: 80%;
  right: 10%;
  width: 20%; }

.squares {
  display: flex;
  flex-wrap: wrap;
  justify-items: center;
  width: 425px;
  margin: 10px auto;
  .square {
    position: relative;
    width: $width/6;
    height: $width/6;
    background-color: black;
    margin: 5px;
    .square__inner {
      position: absolute;
      bottom: 0;
      right: 0;
      background-color: white;
      width: 50%;
      height: 50%; } } }

.letters {
  display: flex;
  flex-wrap: wrap;
  justify-items: center;
  width: 110%;
  margin: 10px auto;
  position: absolute;
  top: 10px;
  left: -30px;
  //.letter
  //  position: relative
  //  width: $width/6
  //  height: $width/6
  //  margin: 5px
  img {
    flex-basis: 20%;
    object-fit: {}
    height: 90px;
    margin: 8px auto;
    width: 80px; } }
@keyframes rotation {
  0% {
    transform: rotate(360deg); }
  50% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes rotation-inverse {
  0% {
    transform: rotate(0deg); }
  50% {
    transform: rotate(360deg); }
  100% {
    transform: rotate(0deg); } }
